$arrow-color: $neutralLighter;
$arrow-size: 3px;
$arrow-max-size: 50px;

.nav-arrow {
  max-width: $arrow-max-size;
  max-height: $arrow-max-size;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;

  .arrow {
    position: absolute;
    border-top: $arrow-size solid $arrow-color;
    border-right: $arrow-size solid $arrow-color;
    width: 50%;
    height: 50%;

    &.primary {
      border-top: $arrow-size solid $primary;
      border-right: $arrow-size solid $primary;
    }

    &.next {
      transform: rotate(45deg);
      left: 18%;
    }

    &.previous {
      transform: rotate(225deg);
      left: 35%;
    }

    &.down {
      transform: rotate(133deg);
      left: 38%;
      top: 32%;
    }

    &.small {
      width: 30%;
      height: 30%;

      &.next {
        left: 30%;
      }

      &.previous {
        left: 40%;
      }

      &.down {
        left: 38%;
        top: 27%;
      }
    }

    &.extra-small {
      width: 25%;
      height: 25%;

      &.next {
        left: 35%;
      }

      &.previous {
        left: 43%;
      }
    }

  }
}
