$flexboxgrid-grid-columns: 12 !default;
$flexboxgrid-gutter-width: 1rem !default;
$flexboxgrid-outer-margin: 1rem !default;

$flexboxgrid-breakpoints:
  sm 23.4375rem 100%, // 375px name/min-width/container-width
  sm2 30rem 100%, // 480px
  md 48rem 100%, // 768px
  md2 64rem 100%, // 1024px
  lg 71.25rem 100%, // 1140px
  lg2 80rem 100% !default; // 1280px
//xl 100rem 100% !default; // 1600px
$flexboxgrid-max-width: 80rem !default;

$gutter-compensation: $flexboxgrid-gutter-width * 0.5 * -1;
$half-gutter-width: $flexboxgrid-gutter-width * 0.5;

.container {
  box-sizing: border-box;
  max-width: $flexboxgrid-max-width;
  margin: 0 auto;
  padding: 0 $half-gutter-width;

  @include breakpoint(md) {
    padding: 0 $flexboxgrid-gutter-width;
  }
}

.container-fluid {
  margin-right: auto;
  margin-left: auto;
  padding-right: $flexboxgrid-outer-margin;
  padding-left: $flexboxgrid-outer-margin;
}

.row {
  box-sizing: border-box;
  display: flex;
  flex: 0, 1, auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: $gutter-compensation;
  margin-left: $gutter-compensation;

  &.hidden-mobile {
    display: none;

    @include breakpoint(md) {
      display: flex;
    }
  }
}

.row.reverse {
  flex-direction: row-reverse;
}

.col.reverse {
  flex-direction: column-reverse;
}

@mixin flexboxgrid-sass-col-common {
  box-sizing: border-box;
  flex-grow: 0;
  flex-shrink: 0;
  padding-right: $half-gutter-width;
  padding-left: $half-gutter-width;
}

$name: xs;

.col-#{$name} {
  @include flexboxgrid-sass-col-common;

  flex-basis: auto;
}

@for $i from 1 through $flexboxgrid-grid-columns {
  .col-#{$name}-#{$i} {
    @include flexboxgrid-sass-col-common;

    flex-basis: 100% / $flexboxgrid-grid-columns * $i;
    max-width: 100% / $flexboxgrid-grid-columns * $i;
  }
}

@for $i from 0 through $flexboxgrid-grid-columns {
  .col-#{$name}-offset-#{$i} {
    @include flexboxgrid-sass-col-common;

    @if $i == 0 {
      margin-left: 0;
    }

    @else {
      margin-left: 100% / $flexboxgrid-grid-columns * $i;
    }
  }
}

.col-#{$name} {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.col-no-gutters {
  padding: 0;
  margin-right: $gutter-compensation;
  margin-left: $gutter-compensation;
}

.col-gutters {
  margin: 0;
  padding-right: $half-gutter-width;
  padding-left: $half-gutter-width;
}

.start-#{$name} {
  justify-content: flex-start;
  text-align: left;
}

.center-#{$name} {
  justify-content: center;
  text-align: center;
}

.end-#{$name} {
  justify-content: flex-end;
  text-align: right;
}

.top-#{$name} {
  align-items: flex-start;
}

.middle-#{$name} {
  align-items: center;
}

.bottom-#{$name} {
  align-items: flex-end;
}

.around-#{$name} {
  justify-content: space-around;
}

.between-#{$name} {
  justify-content: space-between;
}

.first-#{$name} {
  order: -1;
}

.last-#{$name} {
  order: 1;
}

@each $breakpoint in $flexboxgrid-breakpoints {
  $name: nth($breakpoint, 1);
  $size: nth($breakpoint, 2);
  $container: nth($breakpoint, 3);

  @media only screen and (min-width: $size) {
    .container {
      width: $container;
    }

    .col-#{$name} {
      @include flexboxgrid-sass-col-common;

      flex-grow: 1;
      flex-basis: 0;
      max-width: 100%;
    }

    @for $i from 1 through $flexboxgrid-grid-columns {
      .col-#{$name}-#{$i} {
        @include flexboxgrid-sass-col-common;

        flex-basis: 100% / $flexboxgrid-grid-columns * $i;
        max-width: 100% / $flexboxgrid-grid-columns * $i;
      }
    }

    @for $i from 0 through $flexboxgrid-grid-columns {
      .col-#{$name}-offset-#{$i} {
        @include flexboxgrid-sass-col-common;

        @if $i == 0 {
          margin-left: 0;
        }

        @else {
          margin-left: 100% / $flexboxgrid-grid-columns * $i;
        }
      }
    }

    .start-#{$name} {
      justify-content: flex-start;
      text-align: left;
    }

    .center-#{$name} {
      justify-content: center;
      text-align: center;
    }

    .end-#{$name} {
      justify-content: flex-end;
      text-align: right;
    }

    .top-#{$name} {
      align-items: flex-start;
    }

    .middle-#{$name} {
      align-items: center;
    }

    .bottom-#{$name} {
      align-items: flex-end;
    }

    .around-#{$name} {
      justify-content: space-around;
    }

    .between-#{$name} {
      justify-content: space-between;
    }

    .first-#{$name} {
      order: -1;
    }

    .last-#{$name} {
      order: 1;
    }
  }
}
