.header {
  background: $neutral;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  &__logo {
    text-align: right;
    width: 12%;

    img {
      width: 3em;
    }
  }

  &__nav-wrapper {
    width: 50%;
  }

  &__after-nav {
    width: 27%;
    display: none;

    @include breakpoint (md2) {
      display: block;
    }

  }

  &__container {
    display: inline-block;
  }

  &__mobile-nav-wrapper {
    display: inline-block;
    width: 10%;

    @include breakpoint (md2) {
      display: none;
    }
  }

  //scale down the quick link image in the header slightly
  .quick-link__img {
    max-width: 8em;
  }

}
