
.aspect-ratio {
  position: relative;
	height: 0;
	width: 100%;
	overflow: hidden;

	//16x9 Aspect Ratio by default
  padding-bottom: 56.25%;

	/* 4x3 Aspect Ratio */
	&.ratio-4x3 {
		padding-bottom: 75%;
	}

	&.ratio-3x1 {
		padding-bottom: 36.25%;
	}

	/* 1x1 (square) Aspect Ratio */
	&.ratio-1x1 {
		padding-bottom: 100%;
	}

	/* Set child to take up full size of container */
	> * {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
