.breadcrumb {
  ul {
    padding-left: 0;
    list-style: none;

    li {
      display: inline-block;
      font-size: 1.2rem;

      &:not(:last-child) {
        margin-right: 0.5rem;
      }

      a {
        text-decoration: none;
        color: $primary;

        &:focus,
        &:hover {
          color: $primaryDark;
        }
      }
    }
  }
}
