$font-family-title: "Segoe UI","Lato";
$font-family-base: "-apple-system","BlinkMacSystemFont","Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";

@mixin heading {
  line-height: 1.3;
  font-family: $font-family-title;
  color: $primary;
  font-weight: 600;
  letter-spacing: 0.06rem;
  text-transform: uppercase;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  line-height: 1.3;
  font-family: $font-family-title;
  letter-spacing: 0.055rem;
  font-weight: 600;
  text-transform: uppercase;
}

h1,
.h1 {
  font-size: 2.5rem;
  margin: 1rem 0;

  @include breakpoint(md2) {
    font-size: 2.5rem;
  }
}

h2,
.h2 {
  font-size: 1.875rem;
  margin: 1rem 0;
}

h3,
.h3 {
  font-size: 1.75rem;
  margin: 1rem 0;
}

h4,
.h4 {
  font-size: 1.2rem;
  margin: 1rem 0;
}

h5,
.h5 {
  font-size: 1rem;
  margin: 1rem 0;
}

@mixin h3 {
  font-size: 1.75rem;
  margin: 1rem 0;
  line-height: 1.3;
  font-family: $font-family-title;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

@mixin h4 {
  font-size: 1.3rem;
  margin: 1rem 0;
  line-height: 1.3;
  font-family: $font-family-title;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

@mixin h6 {
  font-size: 1rem;
  margin: 0.5rem 0;
}

h6,
.h6 {
  @include h6;
}

@mixin body-font-20 {
  font-size: 1.125rem;

  @include breakpoint(md2) {
    font-size: 1.25rem;
  }
}

@mixin body-font-18 {
  font-size: 1.125rem;
}

.body-font-20 {
  @include body-font-20;
}

.body-font-18 {
  @include body-font-18;
}

blockquote {
  font-family: $font-family-title;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.02rem;
  text-transform: uppercase;
}

ul li,
ol li {
  padding-bottom: 1rem;
  padding-left: 0.5rem;

  &:last-child {
    padding-bottom: 0;
  }

  & > ul li,
  & > ol li {
    &:last-child {
      padding-bottom: 0;
    }

    &:first-child {
      padding-top: 1rem;
    }
  }
}

.chevron-arrow {
  border: solid $primary;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 2px;
  margin: 0 5px;
}

.right {
  transform: rotate(-45deg);
}

.left {
  transform: rotate(135deg);
}

.up {
  transform: rotate(-135deg);
}

.down {
  transform: rotate(45deg);
}

