$accordionHeaderColour: $color-black;
$accordionHeaderColourHover: lighten($accordionHeaderColour, 30%);
$chevronBackgroundColour: $neutral;

.accordion-item {
  display: flex;
  flex-flow: column;
  margin-bottom: 1em;
  overflow: hidden;
  border-radius: 5px;

  &__link {
    text-decoration: none;
    color: $accordionHeaderColour;

    &:hover {
      text-decoration: none;
      color: $accordionHeaderColourHover;
    }
  }

  &__header {
    padding: 0 0 0 1em;
    background-color: $neutral;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  &__body {
    border: .1em solid $neutral;
    padding: .5em;
    display: none;
  }

  &__chevron {
    background: $primary;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
  }

  &__chevron-down {
    position: relative;
    display: inline-block;
    transform: rotate(0deg);
    height: 8px;
    width: 19px;
    margin: 0 auto;
    transition: transform 0.3s ease;
  }

  &__chevron-down::before, &__chevron-down::after {
    position: absolute;
    display: block;
    content: "";
    border: 10px solid transparent; /*adjust size*/
  }

  &__chevron-down::before {
    top: 0;
    border-top-color: $color-white; /*Chevron Color*/
  }

  &__chevron-down::after {
    top:-0.2em; /*adjust thickness*/
    border-top-color: $chevronBackgroundColour; /*Match background colour*/
  }
}

.js-accordion-open {
  .accordion-item__body {
    display: block;
  }
  .accordion-item__chevron-down {
    transform: rotate(180deg);
    margin: 0 auto;
  }
}

.accordion-listing {
  margin-top: 2em;

  &__accordion-items {
    margin-left: 1em;
  }
}


