.banner {
  display: flex;
  flex-direction: column;
  background-color: $neutral;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  min-height: 30em;
  max-width: 100%;
  background-size: cover;
  background-position: center;

  &__banner-content {
    width: 90%;
    padding: 0.5em 1em;
    align-items: center;
    text-align: center;

    @include breakpoint (md2) {
      width: 65%;
    }
  }

  &__ctas {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1em;
    flex-wrap: wrap;

    &--plain {
      display: none;
    }

    &--single {
      flex-direction: column;
    }

    &--double {
      flex-direction: column;

      @include breakpoint (md2) {
        flex-direction: row;
      }

    }
  }

  &__subtext {
    font-size: 1.2em;
    color: $color-white;
    opacity: 1;
  }

  h1 {
    color: $color-white;
    opacity: 1;
  }

  &__cta-wrapper {

    &:first-child {
      flex: 0 0 100%;
      text-align: center;
    }
  }

}

// only show the opaque dark background behind the text is an image is present
.responsive-img {
  .banner__banner-content {
    background: darken($neutral, 65%);
    opacity: 0.8;
  }
}
