// VARS
$color-cs-summary-bg: green;   // colour of summary areas

// Override slider colours
$color-csrs: red; // fill colour
$color-csrs-fill:red; // fill colour from min to current value
$color-csrs-handle-bg:red; // thumb background colour
$color-csrs-handle-border:red; // thumb border colour
$color-csrs-handle-bg-hover: lightblue; // background color on hover
$color-csrs-handle-border-hover: lightblue; // thumb border colour on hover

.saver-calculator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 700px; //FIX for responsive
  margin: 0 auto;

  &__heading {
    text-align: center;
  }

  &__sub-heading {
    text-align: center;
    font-size: 20px;
    text-transform: initial;
    font-weight: bold;
  }

  &__outer-container {
    display: flex;
    flex-direction: row;
  }

  &__inner-container {
    display: flex;
    flex-direction: column;

    &--lhs {
      flex-grow: 1;
    }
  }

  &__expense {
    display: flex;

    &__spend {
      flex-grow: 1;
      padding: 5px 25px 15px 0;
      min-width: 300px; // FIX for responsive
    }

    &__spend-text-container {
      display: flex;
    }

    &__spend-label {
      display: block;
      font-weight: 500;
    }

    &__spend-value {
      margin-left: auto;
      font-weight: bold;

      &::before {
        content: '$';
        display: inline;
        position: relative;
        font-weight: bold;
      }
    }

    &__spend-slider {
      display: block;
      width: 100%;
    }

    &__saving {
      border: 2px solid $color-gray;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      padding: 0;
      height: 50px;
      width: 80px;
      margin: 18px 20px 0 20px;
      background-color: $color-white;
      border-radius: 10px 0 0 10px;

      &::before {
        content: "+";
        display: flex;
        position: absolute;
        width: 20px;
        height: 20px;
        font-weight: 700;
        color: $color-white;
        background-color: $color-cs-summary-bg;
        border-radius: 999px;
        top: 15px;
        left: -10px;
        margin: 0;
        padding-bottom: 4px;
        align-items: center;
        justify-content: center;
      }
    }

    &__saving-value {
      display: block;
      position: relative;
      text-align: right;
      padding-top: 5px;
      padding-right: 5px;
      font-size: 18px;
      font-weight: 700;

      &::before {
        content: "$";
        display: inline-block;
        position: relative;
      }
    }
  }

  &__savings-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-evenly;
    min-height: 300px;
    max-width: 200px;
    color: $color-white;
    background-color: $color-cs-summary-bg;
    border: $color-cs-summary-bg solid 1px;
    border-radius: 5px;
    text-align: center;
  }

  &__saving {
    font-size: 12px;
    font-weight: bold;
  }

  &__saving-value {
    display: block;
    font-size: 26px;

    &::before {
      content: '$';
      position: relative;
      display: inline-block;
    }
  }

  &__saving-or {
    position: relative;
    display: block;
    margin: 0 auto;
    height: 25px;
    width: 25px;
    color: $color-green;
    background-color: $color-white;
    border-radius: 1000px;
    padding: 0;
    font-weight: 700;

    &::before {
      content: "";
      position: absolute;
      display: block;
      color: $color-white;
      border: 1px solid $color-white;
      top: 12px;
      right: 30px;
      width: 45px;
      height: 2px;
    }

    &::after {
      content: "";
      position: absolute;
      display: block;
      color: $color-white;
      top: 12px;
      left: 30px;
      border: 1px solid $color-white;
      height: 2px;
      width: 45px;
    }
  }

  &__savings-button {
    font-size: 14px;
    color: $color-white;
    background-color: $color-green;

    &--filled {
      color: $color-cs-summary-bg;
      background-color: $color-white;
    }
  }

  &__cards-container {
    display: none;  // hide section for now
    // display: flex;
    flex-direction: row;
    margin-top: 25px;
    max-width: 350px; // FIX for responsive
  }

  &__cards-top {
    text-align: left;
  }

  &__cards-dropdown-container {
    margin-left: auto;
  }

  // override defaults for slider
  .slider {
    // main range for slider
    .rangeslider {
      color: $color-rs;
      margin: 5px 0;
    }
    // slider fill from min value to current
    .rangeslider__fill {
      background: $color-csrs-fill;
    }
    // slider horizontal layout
    .rangeslider--horizontal {
      height: 6px;

      .rangeslider__handle {
        top: -9px;
      }
    }

    // slider thumb
    .rangeslider__handle {
      background-image: url("/images/icons/v-hmbgr.png");
      background-color: $color-csrs-handle-bg;
      border-color: $color-csrs-handle-border;
      width: 25px;
      height: 25px;

      &::after {
        display: none;
      }
      // styling when user changing value
      &:active {
        background-image: url("/images/icons/v-hmbgr.png");
        background-color: $color-csrs-handle-bg-hover;
        border-color: $color-csrs-handle-border-hover;
      }
    }
  }

  // candidate for common styling or mixin
  // REMOVE
  // button {
  //   position: relative;
  //   font-weight: 700;
  //   border: 2px solid $color-white;
  //   border-radius: 1000px;
  //   overflow: hidden;
  //   margin: 5px auto 0 auto;
  //   padding: 5px 5px;
  //   min-width: 130px;

  //   &::before {
  //     content: "";
  //     display: block;
  //     position: absolute;
  //     top: 0;
  //     left: 0;
  //     height: 100%;
  //     width: 100%;
  //     z-index: 1;
  //   }

  //   &:hover::before {
  //     background-color: rgba(0, 0, 0, 0.2);
  //   }

  //   > span {
  //     position: relative;
  //     z-index: 2;
  //     opacity: 1;
  //   }
  // }
}
