* {
  box-sizing: border-box;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

html {
  font-size: 100%;
  background: $neutralLighter;
  overflow-x: hidden;
}

body {
  font: 300 1rem/1.5 $font-family-base, sans-serif;
  color: $neutral;
}

body,
html {
  min-height: 100vh;
}

.centre {
  text-align: center;
}

a {
  text-decoration: underline;
  color: $primary;

  &:hover,
  &:focus {
    text-decoration: none;
    color: $primaryDarker;
  }
}

.headline {
  line-height: 1.2rem;
  position: relative;
}

.headline::after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 50%;
  width: 100px;
  border: 1px solid $neutralDark;
  transform: translateX(-50%);
}

