::selection {
  background: $neutralDarker;
  color: $neutralLighter;
  text-shadow: none;
}

::-moz-selection {
  background: $neutralDarker;
  color: $neutralLighter;
  text-shadow: none;
}

::-webkit-selection {
  background: $neutralDarker;
  color: $neutralLighter;
  text-shadow: none;
}

.visually-hidden,
.hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.main {
  margin-top: 0;
}

.popover {
  color: $neutralLighter;
  font-size: 0.8rem;
  background-color: $primaryDarker;
  border-color: $primaryDark;
}

.popover.popover-top {
  padding-bottom: 0;
  margin-bottom: 0;
}

.popover.popover-top::after,
.arrow::after {
  border-top-color: $primaryDark;
}
