@import "~rangeslider.js/dist/rangeslider.css"; // rangeslider.js npm module styling

// Colors
$color-rs: $neutral !default; // fill colour
$color-rs-fill: $primary !default; // fill colour from min to current value
$color-rs-handle-bg: $primary !default; // thumb background colour
$color-rs-handle-border: $primaryDark !default; // thumb border colour
$color-rs-handle-bg-hover: $primaryLighter !default; // background color on hover
$color-rs-handle-border-hover: $primaryLighter !default; // thumb border colour on hover

.slider {
  // main range for slider
  .rangeslider {
      color: $color-rs;
  }
  // slider fill from min value to current
  .rangeslider__fill {
      background: $color-rs-fill;
  }
  // slider horizontal layout
  .rangeslider--horizontal {
      margin: 25px 0;

      .rangeslider__handle {
          opacity: 1; // pass
      }
  }
  // slider vertical layout
  .rangeslider--vertical {
    margin-right:30px;

    .rangeslider__handle {
        opacity: 1; //pass
    }
  }
  // slider thumb
  .rangeslider__handle {
    // background-image: url("/images/icons/v-hmbgr.png");
    background-color: $color-rs-handle-bg;
    border-color: $color-rs-handle-border;

    &:after {
        display:none;
    }
    // styling when user changing value
    &:active {
        // background-image: url("/images/icons/v-hmbgr.png");
        background-color: $color-rs-handle-bg-hover;
        border-color: $color-rs-handle-border-hover;
    }
  }
  // Make thumb square
  &.square {
    .rangeslider,
    .rangeslider__handle,
    .rangeslider__fill {
        -webkit-border-radius: 0;
        border-radius: 0;
    }
  }
}
