.util-reactive-images {
  img {
    opacity: 1;
    transition: all 0.2s;
  }
}

.util-reactive-images-hidden {
  img {
    opacity: 0.1;
  }
}

.util-img-responsive {
  display: block;
  height: auto;
  width: 100%;
}

// Force an image's height. Need wen img inside a side-by-side that has a border
// otherwise the image appears shorter than the container.
.util-img-100-percent--height {
  height: 100%;
}

.util-responsive-child-images {
  img {
    display: block;
    height: auto;
    width: 100%;
  }
}

.util-placeholder-image {
  width: 100%;
  display: block;
}

.toggleable {
  display: none;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.color-primary {
  color: $primary;
}

.color-secondary {
  color: $secondary;
}

.border-top-neutral {
  border-top: 1px solid $neutral;
}

.border-bottom-neutral {
  border-bottom: 1px solid $neutral;
}

.background-primary {
  background-color: $primary;
}

.background-secondary {
  background-color: $secondary;
}

.toggle-section {
  .btn.hidden {
    display: none;
  }
}

.pointer {
  cursor: pointer;
}

.cover-img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  object-fit: cover;
  font-family: 'object-fit: cover;'; //polyfill
}

.scEmptyPlaceholder {
  min-width: 200px;
}
