.quick-link {
  color: $primary;

  margin-right: 1em;
  &__link {
    display: inline-flex;
    align-items: center;
    text-decoration: none;

    &:hover {
      text-decoration: none;

      .quick-link__chevron-right {
        transform: rotate(-90deg) translateY(5px);
      }
    }
  }

  &__chevron-right {
    position: relative;
    display: inline-block;
    transform: rotate(-90deg);
    height: 12px;
    width: 14px;
    margin-left: 0.5em;
    transition: transform 0.3s ease;
  }

  &__chevron-right::before, &__chevron-right::after {
    position: absolute;
    display: block;
    content: "";
    border: 6px solid transparent; /*adjust size*/
  }

  &__chevron-right::before {
    top: 0;
    border-top-color: $primary; /*Chevron Color*/
  }

  &__chevron-right::after {
    top:-3px; /*adjust thickness*/
    border-top-color:#fff; /*Match background colour*/
  }

  &__img {
    max-width: 10em;
  }
}
