// candidate for common styling or mixin
.campaign-button {
  position: relative;
  font-weight: 700;
  background-color: $primary;
  border: 2px solid $color-white;
  border-radius: 1000px;
  overflow: hidden;
  margin: 5px auto 0 auto;
  padding: 5px 5px;
  min-width: 130px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
  }

  &:hover::before {
    background-color: rgba(0, 0, 0, 0.2);
  }

  > span {
    position: relative;
    z-index: 2;
    opacity: 1;
  }
}
