.addthis_toolbox {
  ul {
    padding-left: 0;
    list-style: none;

    li {
      display: inline-block;
      font-size: 1.2rem;

      &:not(:last-child) {
        margin-right: 0.5rem;
      }

      a {
        text-decoration: none;
        color: $primaryLighter;

        i {
          &.fa.fa-circle {
            color: $primaryDark;
          }
        }

        &:focus,
        &:hover {
          color: $primaryDark;

          i {
            &.fa.fa-circle {
              color: $primaryLighter;
            }
          }
        }
      }
    }
  }
}
