.secondary-footer {
  background-color: $tertiary;
  padding: 2em;
  color: $color-white;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  @include breakpoint (md2) {
    flex-direction: row;
    justify-content: space-between;
    text-align: inherit;
  }

  &__nav {
    width: 100%;

    @include breakpoint (md2) {
      width: 80%;
      padding-left: 10em;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0
  }

  &__list-item {
    padding: 0;

    @include breakpoint (md2) {
      padding: 0 1em;
      display: inline-block;
    }

    &:not(:last-child) {

      @include breakpoint (md2) {
        border-right: 0.1em solid $color-white;
      }

    }
  }


  &__list-item-link {
    color: $color-white;

    &:hover {
      text-decoration: none;
      font-weight: 2em;
      color: $color-white;
    }
  }

  &__after-nav {
    width: 100%;

    @include breakpoint (md2) {
      width: 20%;
    }
  }
}
