// === Colour Variables ===
$primary:#2e8ec1;
$primaryDarker:darken($primary, 20%);
$primaryDark:darken($primary, 10%);
$primaryLight:lighten($primary, 10%);
$primaryLighter:lighten($primary, 20%);

$secondary:#945022;
$secondaryDarker:darken($secondary, 20%);
$secondaryDark:darken($secondary, 10%);
$secondaryLight:lighten($secondary, 10%);
$secondaryLighter:lighten($secondary, 20%);

$neutral:#999;
$neutralDarker:darken($neutral, 20%);
$neutralDark:darken($neutral, 10%);
$neutralLight:lighten($neutral, 10%);
$neutralLighter:lighten($neutral, 20%);

$tertiary:#000;
$tertiaryDarker:darken($tertiary, 20%);
$tertiaryDark:darken($tertiary, 10%);
$tertiaryLight:lighten($tertiary, 10%);
$tertiaryLighter:lighten($tertiary, 20%);

$accent:#000;
$accentDarker:darken($accent, 20%);
$accentDark:darken($accent, 10%);
$accentLight:lighten($accent, 10%);
$accentLighter:lighten($accent, 20%);

$color-white: #FFF;
$color-black: #000;
$color-gray: #909090;
$color-red: #D20415;
$color-green:#008000;
