.page {
  text-align: center;

  &--ontop {
    background-color: $neutralLighter;
    margin: 3rem 0;
    padding: 5rem 0 8rem;
  }

  p {
    font-size: 1.125rem;
    font-weight: 600;
    max-width: 650px;
    margin-left: auto;
    margin-right: auto;

    &.page__text--small {
      font-size: 1rem;
      font-weight: 400;
    }
  }

  .btn {
    margin: 0 5px;
  }

  &__icon {
    margin-bottom: 10px;
    width: 146px;
  }

  &-buttons {
    margin-top: 1rem;
  }
}
