@import "~@tarekraafat/autocomplete.js/dist/css/autoComplete.css";

// TODO common button id (it's .campaign-button!)
// TODO implement a colour palette
// TODO make responsive
$color-mydarkgrey: #848484;
$color-mygrey: #E6E6E6;
$color-mylightgrey: #F5F5F5;
$color-mygreyborder: #C7C7C7;
$color-myblue: #2699FB;


// All Hail BEM
.centre-postcode-locator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 50px auto;

  &__heading {
    color:$color-white;
    text-align: center;
    text-transform: none;
  }

  &__form-container {
    padding: 20px 5px;
    background-color: $color-mydarkgrey; //FIX
  }

  &__form {
    opacity:1;  // REMOVE
  }

  &__fieldset {
    margin: 35px 5px 10px 5px;
  }

  // You cannot put flex on a fieldset element
  &__field-outer-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  &__field-container {

    &--hidden {
      display:none;
    }
  }

  &__field-input {
    height: 35px;
    border: none;
    border-radius:5px;
    padding: 0 5px;
    margin: 0 8px;
  }

  &__label {
    opacity:1; //REMOVE
  }

  &__button {
    color: $color-white;
    background-color: $color-myblue; //FIX
    border: none;
    border-radius: 5px;
    letter-spacing: 1.1px;

    &:hover {
      opacity: 1; //REMOVE
    }
  }

  &__submit-button {
    height: 35px;
    margin: 0 8px 0 50px;
  }

  &__find-out-more-button-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
  }

  &__find-out-more-button {
    min-height: 25px;
    max-width: 150px;
    padding: 0;
    line-height: 14px;
    font-size: 12px;
    border-radius: 2px;
    margin: 0;
    padding: 0 20px;

  }

  &__centre-postcode-locator__results-container {
    opacity: 1;  //REMOVE
  }

  &__backbutton {
    position: relative;
    padding: 5px;
    border: none;
    background-color: transparent;
    width: 100%;
    text-align: left;

    &:before {
      position: relative;
      content: "<";
      padding-right: 5px;

    }

    &:hover {
      color: $color-myblue; //FIX
      text-decoration: underline;
    }
  }

  // Slide in/out div
  &__slider {
    position: absolute;
    background: $color-mylightgrey;
    width: 550px;
    height: 100%;
    padding: 10px;
    overflow-y: auto;
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
    z-index: 1;
  }

  //FIX make modifier of slider element
  .slide-in {
      animation: slide-in 0.2s forwards;
      -webkit-animation: slide-in 0.2s forwards;
      box-shadow: 10px 0 5px -2px #888; //FIX
  }

  //FIX make modifier of slider element
  .slide-out {
      animation: slide-out 0.2s forwards;
      -webkit-animation: slide-out 0.2s forwards;
  }

  @keyframes slide-in {
      100% { transform: translateX(0%);
            }
  }

  @-webkit-keyframes slide-in {
      100% { -webkit-transform: translateX(0%); }
  }

  @keyframes slide-out {
      0% { transform: translateX(0%); }
      100% { transform: translateX(-100%);
    opacity:0;}
  }

  @-webkit-keyframes slide-out {
      0% { -webkit-transform: translateX(0%); }
      100% { -webkit-transform: translateX(-100%);
    opacity:0;}
  }

  &__results-map-container {
    display: flex;
    flex-direction: row;
    position: relative;
    overflow: hidden;
  }

  &__results-container {
    min-width:500px;  //FIX
    overflow-y: auto;
    background-color: $color-mylightgrey; //FIX
  }

  &__search-result {
    display: flex;
    flex-direction: row;
    background-color: $color-white;
    border: solid 1px $color-mygreyborder; // FIX
    border-radius: 5px;
    margin: 15px;
    padding: 10px;

    &:hover{
      background-color: $color-mygrey;
    }
  }

  &__image-container{
    opacity: 1; // REMOVE
  }

  &__image-wrapper{
    opacity: 1; // REMOVE
  }

  &__image{
    max-width: 250px;
  }

  &__map-container {
    flex-grow:1;
    height: 700px; //FIX
    width:100%;
  }

  &__location-container {
    display: flex;
    flex-direction: column;
    margin-left: 7px;
  }

  &__location-text {
    font-size: 16px;
    font-weight: 500px;

    &--bold {
      font-weight: 700;
    }

    &--small {
      font-size: 12px;
    }
  }
}
